<template>
  <div>
    <div v-if="key">
      你的解压提取码是: {{ key }}
      <button @click="changeKey">
        更改
      </button>
    </div>
    <div v-else>
      请输入解压提取码: <input type="text" v-model="keyChecked" />
      <button @click="saveKey">
        确定
      </button>
    </div>
    hash：<br />
    {{ hash }}<br />
    <button @click="encryption">
      解压⬇️
    </button>
    <br />
    <!-- <textarea rows="10" cols="30" v-model="hash" /> -->
    <br />
    <div v-if="text">
      {{ text }}
      <br />
    </div>
  </div>
</template>

<script>
const crypto = require("crypto");
function aesDecrypt(encrypted, key) {
  const decipher = crypto.createDecipher("aes192", key);
  var decrypted = decipher.update(encrypted, "hex", "utf8");
  decrypted += decipher.final("utf8");
  return decrypted;
}
export default {
  name: "encryption",
  created() {
    this.key = localStorage.getItem("encryption-key") || "";
    this.hash = this.$route.query.hash || "";
    if (this.hash) {
      this.encryption();
    }
  },
  data() {
    return { key: "", hash: "", text: "" };
  },
  methods: {
    saveKey() {
      if (!this.keyChecked) return;
      this.key = this.keyChecked;
      localStorage.setItem("encryption-key", this.key);
    },
    changeKey() {
      this.keyChecked = "";
      this.key = "";
      this.url = "";
      localStorage.removeItem("encryption-key");
    },
    encryption() {
      var decrypted = aesDecrypt(this.hash, this.key);
      this.text = decrypted;
    }
  }
};
</script>

<style scoped></style>
