<template>
  <div>
    <div v-if="key">
      你的打包提取码是: {{ key }}
      <button @click="changeKey">
        更改
      </button>
    </div>
    <div v-else>
      请输入打包提取码: <input type="text" v-model="keyChecked" />
      <button @click="saveKey">
        确定
      </button>
    </div>
    <textarea rows="10" cols="30" v-model="text" /><br />
    <button @click="decrypt">
      打包⬇️
    </button>
    <br />
    <!-- <textarea rows="10" cols="30" v-model="hash" /> -->
    <br />
    <div v-if="url">
      <p style="word-break: break-all;">{{ url }}</p>
      <br />
      <button @click="copy">
        复制
      </button>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-console */
const crypto = require("crypto");
function aesEncrypt(data, key) {
    const cipher = crypto.createCipher('aes192', key);
    var crypted = cipher.update(data, 'utf8', 'hex');
    crypted += cipher.final('hex');
    return crypted;
}

export default {
  name: "decrypt",
  created() {
    this.key = localStorage.getItem("key") || "";
    this.hash = this.$route.query.hash || "";
  },
  data() {
    return {
      key: "",
      keyChecked: "",
      text: "",
      hash: "",
      url: "",
    };
  },
  methods: {
    saveKey() {
      if (!this.keyChecked) return;
      this.key = this.keyChecked;
      localStorage.setItem("key", this.key);
    },
    changeKey() {
      this.keyChecked = "";
      this.key = "";
      this.url = "";
      localStorage.removeItem("key");
    },
    decrypt() {
      if (!this.key) return console.log("缺少key");
      if (!this.text) return console.log("缺少text");
      const encrypted = aesEncrypt(this.text, this.key);
      this.hash =encrypted
      this.url = `${window.location.origin}/#/encryption?hash=${this.hash}`;
    },
    copy() {
    }
  }
};
</script>

<style scoped></style>
